/**
 * Example SSR page: renders a greeting and current time, hydrates on the client.
 */

export async function getServerSideProps() {
  return { message: "Hello from Oxian SSR", now: new Date().toISOString() };
}

export default function Page(props: { message: string; now: string }) {
  return (
    <main style={{ fontFamily: "sans-serif", padding: 24 }}>
      <h1>{props.message}</h1>
      <p>Server time: {props.now}</p>
      <button type="button" onClick={() => alert("Hydration works!")}>Click me</button>
    </main>
  );
}


